import React, { useEffect } from 'react';
import { useState } from 'react';
import { Card, Form, InputGroup } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { BsSearch } from 'react-icons/bs';
import FutureAppointments from './FutureAppointments';
import PastAppointments from './PastAppointments';
import TodayAppointments from './TodayAppointments';

const DoctorAppointment = () => {
    const [activeTabs, setActiveTabs] = useState(0);
    const { register, handleSubmit } = useForm();
    useEffect(() => {
        document.title = "Appointment || Sama Health";
    }, []);
    function onSubmit(data) {
    }
    return (
        <div className='doctor-tables'>
            <Form onSubmit={handleSubmit(onSubmit)}>
                <div className="row">
                    <div className="col-xl-3 col-lg-6 col-md-6 col-sm-12 col-12">
                        <InputGroup className="mb-3">
                            <Form.Control
                                type="text"
                                name="search"
                                placeholder="Search by name"
                                ref={register()}
                            />
                            <InputGroup.Text><BsSearch /></InputGroup.Text>
                        </InputGroup>
                    </div>
                </div>
            </Form>
            <Card className="border-0">
                <Card.Body className='p-0'>
                    <Card.Header>
                        <button
                            onClick={() => {
                                setActiveTabs(0)
                            }}
                            type='button'
                            className={` ${activeTabs === 0 ? "active" : ""}`}
                        >
                            Today Appointments
                        </button>
                        <button
                            onClick={() => {
                                setActiveTabs(1)
                            }}
                            type='button'
                            className={` ${activeTabs === 1 ? "active" : ""}`}
                        >
                            Future Appointments
                        </button>
                        <button
                            onClick={() => {
                                setActiveTabs(2)
                            }}
                            type='button'
                            className={` ${activeTabs === 2 ? "active" : ""}`}
                        >
                            Past Appointments
                        </button>
                    </Card.Header>
                    {
                        activeTabs === 0 ?
                            <TodayAppointments setActiveTabs={setActiveTabs} />
                            :
                            activeTabs === 1 ?
                                <FutureAppointments setActiveTabs={setActiveTabs} />
                                :
                                <PastAppointments setActiveTabs={setActiveTabs} />
                    }

                </Card.Body>
            </Card>
        </div>
    );
}

export default DoctorAppointment;
