import React from 'react';
import { Table } from 'react-bootstrap';
import { IMAGES } from '../../../assets/images';

const PastAppointments = () => {
    return (
        <Table responsive>
            <thead>
                <tr>
                    <th>Member Name</th>
                    <th>Fee</th>
                    <th>Status</th>
                    <th>Date and Time</th>
                    <th>Actions</th>
                </tr>
            </thead>
            <tbody>
                {PastAppointmentsList.map((list, index) => {
                    return (
                        <TableListData list={list} key={index} />
                    );
                })}
            </tbody>
        </Table>
    );
}

export default PastAppointments;
function TableListData({ list }) {
    return (
        <tr>
            <td><img src={IMAGES.PROFILELOGO} className="img-fluid" alt="member-pic" />{list?.memberName}</td>
            <td>{list?.fee}</td>
            <td>
                <span
                    className={`${list?.status === true
                        ? "low_risk_btn"
                        : "medium_risk_btn"
                        }`}
                >
                    {list?.status === true ? "Paid" : "unPaid"}
                </span>
            </td>
            <td>{list?.date}</td>
            <td className='content-start-aligned'>
                <button type='button' className='notes-added-btn'>
                    Notes Added
                </button>
                <button type='button' className='video-call-btn custom-primary-blue-bg border-0'>
                    35 : 12 min Call
                </button>
            </td>
        </tr>
    )
}

