import React from 'react';
import { Table } from 'react-bootstrap';
import { HiVideoCamera } from "react-icons/hi";
import { IMAGES } from '../../../assets/images';
const TodayAppointments = () => {
    return (
        <Table responsive>
            <thead>
                <tr>
                    <th>Member Name</th>
                    <th>Fee</th>
                    <th>Status</th>
                    <th>Date and Time</th>
                    <th>Action</th>
                </tr>
            </thead>
            <tbody>
                {TodayAppointmentsList.map((list, index) => {
                    return (
                        <TableListData list={list} key={index} />
                    );
                })}
            </tbody>
        </Table>
    );
}

export default TodayAppointments;

function TableListData({ list }) {
    return (
        <tr>
            <td><img src={IMAGES.PROFILELOGO} className="img-fluid" alt="member-pic" />{list?.memberName}</td>
            <td>{list?.fee}</td>
            <td>
                <span
                    className={`${list?.status === true
                        ? "low_risk_btn"
                        : "medium_risk_btn"
                        }`}
                >
                    {list?.status === true ? "Paid" : "unPaid"}
                </span>
            </td>
            <td>{list?.date}</td>
            <td>
                <button type='button' className='video-call-btn'>
                    <HiVideoCamera /> Start Call
                </button>
            </td>
        </tr>
    )
}

